

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1em}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}body::-webkit-scrollbar{display:none}body{-ms-overflow-style:none}.content-wrap{width:100vw;height:100vh;display:grid;grid-template-columns:repeat(12, 1fr);grid-template-rows:repeat(auto-fill, 8.3vh)}

.main-nav{width:100vw;height:12vh;position:fixed;z-index:2;top:0;left:0;background:white}.main-nav__menu_wrapper{width:100%;height:100%;display:flex;align-items:center;justify-content:space-between;padding:0 1em;box-sizing:border-box}.main-nav__logo{height:10vh}.main-nav__logo img{height:100%}.main-nav__hamburger{display:block;width:26px;height:16px;position:relative;z-index:3}.main-nav__hamburger i{-webkit-transition:1s;transition:1s;display:block;position:absolute;width:26px;height:2px;background-color:#161616;border-radius:5px}.main-nav__hamburger i:nth-of-type(1){-webkit-transition:1s;transition:1s;top:16px}.main-nav .nav-collapse{background:#161616;position:fixed;top:0;right:-80vw;width:80vw;height:100vh;-webkit-transition:right 1s;transition:right 1s;z-index:2;display:grid;grid-template-columns:repeat(9, 1fr);grid-template-rows:repeat(12, 1fr)}.main-nav .nav-collapse__list{list-style:none;padding:0;margin:0;color:white;grid-column:3/8;grid-row:3/10}.main-nav .nav-collapse__item{margin-bottom:2.3em}.main-nav .nav-collapse__icons{grid-column:3/6;grid-row:11/12;align-self:center}.main-nav .nav-collapse__icons img{width:2.3em;margin-right:0.8em}.showMenu .main-nav__hamburger i{-webkit-transform:rotate(-45deg);transform:rotate(-45deg);top:8px;background-color:white}.showMenu .main-nav__hamburger i:nth-of-type(1){-webkit-transform:rotate(45deg);transform:rotate(45deg);top:8px}.showMenu .nav-collapse{right:0}@media only screen and (min-width: 48em){.main-nav__hamburger{display:none}.main-nav .nav-collapse{max-width:85vw;height:12vh;right:2em;background:white;grid-template-columns:repeat(6, 1fr);grid-template-rows:1fr}.main-nav .nav-collapse__list{color:black;grid-column:1/13;grid-row:1/13;display:flex;justify-content:space-between;align-items:flex-end}.main-nav .nav-collapse__item{margin-bottom:1em}.main-nav .nav-collapse__icons{display:none}}.jumbotron{height:50vh}

.mainSlideBanner{width:inherit;height:inherit}.mainSlideBanner__boxImg{height:55%;background-size:cover;background-repeat:no-repeat;background-position:center}.mainSlideBanner__txtBox{max-height:35%;padding:.8em;box-sizing:border-box}.mainSlideBanner__txtBox .title{margin:0;font-size:1.5em;line-height:1.1em;max-height:36%}.mainSlideBanner__txtBox .paragraph{max-height:38%;line-height:1.1em;margin:0 0 .5em 0}.mainSlideBanner__txtBox .line-clamp{display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;overflow:hidden;padding-bottom:0.2em}.mainSlideBanner__txtBox .see-more-btn{padding:.3em 2.5em;border:none;border-radius:3px;background:#AC90F1;color:white}@media only screen and (min-width: 48em){.mainSlideBanner{grid-row:3/11}.mainSlideBanner__boxImg{height:88%}.mainSlideBanner__txtBox{position:absolute;bottom:12%;width:48%;box-shadow:0 0 0 200px rgba(255,255,255,0.5) inset;max-height:none}}@media only screen and (min-width: 62em){__txtBox{padding-left:1em}}

